import React from 'react';
import {StaticQuery, graphql } from 'gatsby';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import Header from '../components/header';
import Sidebar from '../components/sidebar';
import Footer from '../components/footer';
import { Alert, Button } from 'react-bootstrap';
import '../../sass/style.scss';
import '../../node_modules/react-bootstrap-table/dist/react-bootstrap-table-all.min.css';

class AlertPanel extends React.Component {
  constructor(props) {
    super(props);
    this.checkPrivate = this.checkPrivate.bind(this);
    this.state = { statusResponse: 0 };
  }


  async checkPrivate(e, espnId, url) {
    e.preventDefault();
    const url2020 = `https://fantasy.espn.com/apis/v3/games/ffl/seasons/2020/segments/0/leagues/${espnId}?view=mSetting`;
    const status = await fetch(url2020)
      .then(response => { return response.status })
      .catch(error => {
        console.log(error);
      });
    if (status === 200) {
      window.open(url);
      this.setState({ statusResponse: status });
    }
    else {
      this.setState({ statusResponse: status });
    }
  }

  render() {
    const statusResponse = this.state.statusResponse;
    const isPrivate = this.props.isPrivate;
    const formUrl = "https://docs.google.com/forms/d/e/1FAIpQLScBSHPr7Spm7NI34vLZW4Zkc7tK8rir865SGpDsrRwuEgIWlQ/formResponse?usp=pp_url&entry.1920529043=" + this.props.espnId;

    if (isPrivate) {
      return <Alert key="1" variant="info">
        <Alert.Heading>No 2021 Data </Alert.Heading>
        <MessagePanel status={statusResponse} />
        <Button onClick={e => this.checkPrivate(e, this.props.espnId, formUrl)} variant="info" size="lg">Update</Button>
      </Alert>;
    }
    else {
      return null;
    }
  }
}

function MessagePanel(props) {
  const status = props.status;
  if (status === 200) {
    return (
      <p className="text-info">Your request was succefully made</p>
    )
  }
  else if (status === 0) {
    return (
      <p>It seems that your league is private and therefore I can't get the 2020 data.
        Once you change it public click the following button to request an update. <br /> If you don't how to do that please follow <a target="_blank" rel="noopener noreferrer" href="https://support.espn.com/hc/en-us/articles/360000064451-Making-a-Private-League-Viewable-to-the-Public">this steps</a>.
        </p>
    )
  }
  else {
    return (
      <p className="text-danger">Your league still shows as private</p>
    )
  }
}

class Layout extends React.Component {
  constructor(props) {
    super(props);
    this.sidebarToggle = this.sidebarToggle.bind(this);
    this.state = {sidebarToggled: false};
    this.state = { status: 200 };
  }

  sidebarToggle = () => {
    const doesShow = this.state.sidebarToggled
    this.setState({ sidebarToggled: !doesShow })
  }
  render() {
    const { children, data} = this.props;
    return (
      <StaticQuery
        query={graphql`
          query {
            allDataJson {
              edges {
                node {
                  leagueSettings {
                    leagueName
                    id
                    paid
                    isPrivate
                  }
                } 
              }
            }
          }
        `}
        render={data => (
          <div id="page-top" className={this.state.sidebarToggled ? 'sidebar-toggled' : 'sidebar-untoggled'}>
            <Helmet title="Fantasy Football Franchise Stats" />
            <Header triggerhappy={this.sidebarToggle} paid={data.allDataJson.edges[0].node.leagueSettings[0].paid} leagueName={data.allDataJson.edges[0].node.leagueSettings[0].leagueName}/>
            <div id="wrapper">
              <Sidebar toggled={this.state.sidebarToggled} />
              <div id="content-wrapper">
                <div className="container-fluid">
                  <AlertPanel isPrivate={data.allDataJson.edges[0].node.leagueSettings[0].isPrivate} espnId={data.allDataJson.edges[0].node.leagueSettings[0].id} />
                </div>
                {children}
              </div>
              <Footer />
            </div>
          </div>
        )}
      />
    )
  }
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout;

